$default-border: 1px solid gray;
$border-radius: 5px;

$bg-primary: rgb(255, 251, 0);
$bg-primary-light: rgb(255, 208, 0);
$bg-primary-dark: orangered;

$bg-secondary: lighten(aqua, 30);
$bg-secondary-dark: darken(aqua, 30);

$bg-tertiary: lighten(aquamarine, 5);
$bg-tertiary-dark: darken(aquamarine, 35);

$bg-timebox: #ffaaaa;
$bg-timebox-dark: darken(#ffaaaa, 10);

$bg-current: lighten(yellow, 55);
$bg-current-dark: rgb(255, 238, 0);

$bg-finish: lighten(green, 60);
$bg-finish-dark: lighten(green, 30);