.App {
  font-family: sans-serif;
}

.Timebox,
.TimeboxFinished {
  border: $default-border;
  border-radius: $border-radius;
  padding: 5px;
  margin-bottom: 10px;
}

.TimeboxFinished {
  background-image: linear-gradient(to right bottom, $bg-finish, $bg-finish-dark);
}

.TimeboxEditor,
.TimeboxCreator,
.LoginForm,
.TimeboxSearch {
  text-align: right;
  border: $default-border;
  border-radius: $border-radius;
  padding: 20px;
  margin-bottom: 10px;
}

.TimeboxCreator,
.LoginForm {
  background-image: linear-gradient(to right bottom, $bg-secondary, $bg-secondary-dark);
}

.TimeboxEditor {
  background-image: linear-gradient(to right bottom, $bg-tertiary, $bg-tertiary-dark);
}

.TimeboxSearch {
  background-image: linear-gradient(to right bottom, $bg-primary, $bg-primary-dark);
}

.Timebox {
  background-image: linear-gradient(to right bottom, $bg-timebox, $bg-timebox-dark);
}

.CurrentTimebox {
  background-image: linear-gradient(to right bottom, $bg-current, $bg-current-dark);
}

input {
  min-width: 60%;
  margin-left: 20px;
  font-size: 16px;
  padding-left: 10px;
  border-radius: $border-radius;
  border: $default-border;

}


button {
  font-size: 16px;
  margin-left: 10px;
}

.CurrentTimebox {
  border: 1px solid gray;
  border-radius: 5px;
  padding: 20px;
  text-align: center;
}

.Clock {
  color: orangered;
}

.progress {
  border: 1px solid black;
  border-radius: 5px;
  height: 25px;
  margin-bottom: 20px;
  padding: 5px;

  &--big {
    height: 50px;
  }

  &--color-red {
    border-color: orangered;
  }

  &__bar {
    height: 100%;
    background-color: black;
  }

  &--color-red &__bar {
    background-color: orangered;
  }
}

.header {
  display: flex;
  justify-content: flex-end;
  margin: 15px;

  &__logout-link {
    margin-left: 10px;
  }
}

.LoginForm {
  &__error-message {
    margin: 15px auto;
    text-align: center;
    border: 1px solid red;
    padding: 10px;
    border-radius: 5px;
    color: red;
  }
}

.error {
  color: red;
}